              <el-col :span="12"><attribute attr="安全监管人" :value="installationSite.supervisors"></attribute></el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="12"><attribute attr="项目地址" :value="installationSite.address"></attribute></el-col>
              <el-col :span="12"><attribute attr="设备数量" :value="devicesSummaryTotal"></attribute></el-col>
            </el-row>
            <el-row :gutter="24">
              <el-col :span="12"><attribute attr="项目标签" :value="projectlabel"></attribute></el-col>
            </el-row>
          </div>
        </el-col>
        <el-col :span="4">
          <div class="info-alarms">
            <div>
              <img src="@/assets/img/alarm.png" alt="">
              <p>未处理报警</p>
            </div>
            <div>
              <img src="@/assets/img/trouble.png" alt="">
              <p>设备故障</p>
            </div>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="equipments">
      <title-header title="下属设备及探测器"></title-header>
      <div class="equipment-list">
        <equipment-item v-for="(item, index) in showType" :key="index" :data="item" :keyindex="index" @toDetails="handleDetails(item)"></equipment-item>
      </div>
    </div>
