<script>
import TitleHeader from '@/components/titleHeader'
import attribute from '@/components/attribute'
import equipmentItem from './components/equipment-item'
import { countPlaceFFhost, placeDevices, placeVideos, placeCountPatrol, pageAllAlarms, getenergytoday } from '@/api/device'
import msgUtil from '@/utils/msgUtil'
import constant from '@/common/constant'
import Loading from '@/components/loading'

export default {
  name: 'Equipment',
  components: {
    TitleHeader,
    attribute,
    equipmentItem,
    Loading
  },
  mounted() {
    this.setPlaceList()
  },
  computed: {
    devicesSummaryTotal() {
      return this.devicesSummary.smoke.total + this.devicesSummary.gas.total + this.devicesSummary.leakageTemperature.total + this.devicesSummary.fireHydrantWaterPress.total + this.devicesSummary.waterLevel.total + this.devicesSummary.fireEngine.total + this.devicesSummary.elestric.total + this.devicesSummary.oneBtnAlarm.total + this.devicesSummary.shakeAlarm.total + this.devicesSummary.monitor.total + this.devicesSummary.patrol.total + this.devicesSummary.temperature.total + this.devicesSummary.patrolassistant.total
    },
    projectlabel() {
      return this.installationSite.labels ? this.installationSite.labels.map(i => i.name).join(',') : '-'
    },
    showType() {
      // 列表展示的装备
      return [
        { name: '智能烟感', subname: '烟雾监测，火灾预警', alarmsNum: this.devicesSummary.smoke.alarmNum, troubleNum: this.devicesSummary.smoke.troubleNum, total: this.devicesSummary.smoke.total, code: 2, key: 'smoke' },
        { name: '气体报警', subname: '可燃气体监测', alarmsNum: this.devicesSummary.gas.alarmNum, troubleNum: this.devicesSummary.gas.troubleNum, total: this.devicesSummary.gas.total, code: 5, key: 'gas' },
        { name: '用电安全', subname: '智能电气火灾监控', alarmsNum: this.devicesSummary.leakageTemperature.alarmNum, troubleNum: this.devicesSummary.leakageTemperature.troubleNum, total: this.devicesSummary.leakageTemperature.total, code: 21, key: 'leakageTemperature' },
        { name: '消防栓', subname: '消防水压监测', alarmsNum: this.devicesSummary.fireHydrantWaterPress.alarmNum, troubleNum: this.devicesSummary.fireHydrantWaterPress.troubleNum, total: this.devicesSummary.fireHydrantWaterPress.total, code: 22, key: 'fireHydrantWaterPress' },
        { name: '水位仪', subname: '消防水箱水位监测', alarmsNum: this.devicesSummary.waterLevel.alarmNum, troubleNum: this.devicesSummary.waterLevel.troubleNum, total: this.devicesSummary.waterLevel.total, code: 23, key: 'waterLevel' },
        { name: '消防主机联网', subname: '传统消防的智能改造', alarmsNum: this.devicesSummary.fireEngine.alarmNum, troubleNum: this.devicesSummary.fireEngine.troubleNum, total: this.devicesSummary.fireEngine.total, code: 24, key: 'fireEngine' },
        { code: 7, name: '一键报警', subname: '紧急呼叫，一键报警', alarmsNum: this.devicesSummary.fireEngine.alarmNum, troubleNum: this.devicesSummary.fireEngine.troubleNum, total: this.devicesSummary.fireEngine.total, key: 'fireEngine' },
        { code: 26, name: '巡更助手', subname: '紧急呼叫，一键报警', alarmsNum: this.devicesSummary.patrolassistant.alarmNum, troubleNum: this.devicesSummary.patrolassistant.troubleNum, total: this.devicesSummary.patrolassistant.total, key: 'patrolassistant' },
        { code: 15, name: '智能温感', subname: '温度监测，适用于着火预警', alarmsNum: this.devicesSummary.temperature.alarmNum, troubleNum: this.devicesSummary.temperature.troubleNum, total: this.devicesSummary.temperature.total, key: 'temperature' },
        { code: null, name: '防震动报警', subname: '防盗，防触碰监控', alarmsNum: this.devicesSummary.shakeAlarm.alarmNum, troubleNum: this.devicesSummary.shakeAlarm.troubleNum, total: this.devicesSummary.shakeAlarm.total, key: 'shakeAlarm' },
        { code: 'video', name: '视频监控', subname: '消防系统视频联动', alarmsNum: this.devicesSummary.monitor.alarmNum, troubleNum: this.devicesSummary.monitor.troubleNum, total: this.devicesSummary.monitor.total, key: 'monitor' },
        { code: 'patrol', name: '巡检维保', subname: '维保产品，一键巡检', alarmsNum: this.devicesSummary.patrol.alarmNum, troubleNum: this.devicesSummary.patrol.troubleNum, total: this.devicesSummary.patrol.total, key: 'patrol' },
        { code: 11, name: '智能用电终端', subname: '监测用电情况，规范用电', alarmsNum: this.devicesSummary.elestric.alarmNum, troubleNum: this.devicesSummary.elestric.troubleNum, total: this.devicesSummary.elestric.total, key: 'elestric' },
        // { code: 229, name: '工业燃气', subname: '监测工业产生的燃气，规范生产', alarmsNum: this.devicesSummary.ingas.alarmNum, troubleNum: this.devicesSummary.ingas.troubleNum, total: this.devicesSummary.ingas.total, key: 'ingas' },
        // { code: 31, name: '控制柜监测', subname: '实时监测控制柜状态', alarmsNum: this.devicesSummary.conmon.alarmNum, troubleNum: this.devicesSummary.conmon.troubleNum, total: this.devicesSummary.conmon.total, key: 'conmon' },
        { code: null, name: '防火门', subname: '具有阻止火势蔓延和烟气扩散的作用', alarmsNum: 0, troubleNum: 0, total: 0, key: '' },
        { code: null, name: '水泵设备', subname: '输送液体介质', alarmsNum: 0, troubleNum: 0, total: 0, key: '' }
      ]
    },
    installUnit() {
      console.log(this.$store.state.place.buildings)
      return Object.keys(this.$store.state.place.buildings).length || 0
    },
    isloading() {
      return this.$store.state.place.loading
    },
    // listData() {
    //   const keyWord = this.filterVal
    //   // console.log(keyWord)
    //   let keys = []
    //   // console.log(this.$store.state.place.all)
    //   if (this.$store.state.place.all.length > 0) {
    //     keys = Object.keys(this.$store.state.place.all[0])
    //   }
    //   return this.$store.state.place.all.filter(p => {
    //     return keys.some(key => {
    //       if (p[key] && p[key].indexOf) {
    //         // console.log(p)
    //         return p[key].indexOf(keyWord) >= 0
    //       } else {
    //         return false
    //       }
    //     })
    //   })
    // },
    installationSite() {
      const item = this.$store.state.place.checkItem
      return {
        id: item.place.id,
        name: item.place.name,
        agentName: item.place.company,
        buildyear: item.place.build_year,
        household: item.place.household,
        households: item.place.households,
        labels: item.labels,
        projectLeader: item.projectLeader || {},
        supervisors: item.supervisors.map(s => { return s.name }).join('、'),
        address: `${item.place.province || ''}${item.place.city || ''}${item.place.county || ''}${item.place.village || ''}${item.place.street || ''}`,
        devicesNum: item.place.devices,
        clientPrincipal: {
          name: item.place.client_agent,
          tel: item.place.client_tel
        },
        occPrincipal: {
          name: item.place.occ_agent,
          tel: item.place.occ_tel
        }
      }
    }
  },
  data() {
    return {
      loading: false,
      listData: [],
      devicesSummary: {
        alarmAndTrouble: {
          alarmsNum: 0,
          troublesNum: 0
        },
        smoke: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        njsmoke: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        gas: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        nbsmoke: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        nbgas: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        ingas: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        conmon: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        electricalFire: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        leakageTemperature: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        fireHydrantWaterPress: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        waterLevel: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        fireEngine: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        elestric: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        oneBtnAlarm: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        patrolassistant: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        nboneBtnAlarm: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        shakeAlarm: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        monitor: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        patrol: {
          done: 0,
          undo: 0,
          total: 0
        },
        temperature: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        },
        obstacle: {
          alarmNum: 0,
          troubleNum: 0,
          total: 0
        }
      }
    }
  },
  watch: { // watch主要用于监控vue实例的变化，它监控的变量当然必须在data里面声明才可以，
    'installationSite.id': function(val) {
      if (val !== undefined) {
        this.initDevicesInfo(val)
        // this.initDeviceslist(val)
        // clearInterval(timer)
      }
    }
  },
  methods: {
    async setPlaceList() {
      try {
        this.loading = true
        await this.$store.dispatch('setPlaceList', this.$route.params.id)
        this.loading = false
      } catch (error) {
        this.loading = false
        msgUtil.error('加载数据失败', msgUtil.findErrorMsg(error))
      }
    },
    initDevicesInfo(placeId) {
      Object.keys(constant.deviceTypes).forEach((key) => {
        if (this.devicesSummary[key]) {
          if (key === 'fireEngine') {
            countPlaceFFhost(placeId).then(resp => {
              this.devicesSummary.fireEngine.total = resp.data
            })
          } else {
            placeDevices(placeId, { page: 1, per_page: 1, type: constant.deviceTypes[key].code, wise_type: 'ALL' }).then(resp => {
              if (key !== 'monitor' && key !== 'fireEngine') {
                this.devicesSummary[key].total = resp.data.page_info.total
                if (key == 'elestric') {
                  this.getenergy(placeId)
                }
              }
            }, error => {
              msgUtil.error('加载设备数据失败', msgUtil.findErrorMsg(error))
            })
          }
        }
      })
      placeVideos(placeId, { page: 1, per_page: 1 }).then(resp => {
        this.devicesSummary.monitor.total = resp.data.page_info.total
      })
      placeCountPatrol(placeId).then(resp => {
        // console.log(resp.data)
        this.devicesSummary.patrol.total = resp.data
      })
      pageAllAlarms({
        page: 1,
        per_page: 1,
        start: Math.ceil(Date.now() / 1000 - 7 * 24 * 60 * 60),
        end: Math.ceil(Date.now() / 1000),
        place_id: placeId,
        notice_status: 3
      }).then(resp => {
        this.devicesSummary.alarmAndTrouble.alarmsNum = resp.data.page_info.total
      }, error => {
        msgUtil.error('加载设备数据失败', msgUtil.findErrorMsg(error))
      })
      // console.log(this.devicesSummary)

    },
    getenergy(id) {
      id = id + ''
      if (this.devicesSummary.elestric.total) {
        getenergytoday({ pid: id }).then(resp => {
          this.totalpower = resp.data
        })
        // var that = this
        // clearInterval(timer)
        // timer = setInterval(function() {
        //   http.getenergytoday({ pid: id }).then(resp => {
        //     that.totalpower = resp.data
        //   })
        // }, 900000)
      }
    },
    handleDetails(item) {
      const placeid = this.$store.state.place.checkItem.place.id
      this.$router.push({
        path: `/equipment/devices-list/${item.code}`,
        query: {
          name: item.name,
          placeid
        }
      })
    }
  }
}
</script>

