import constant from '@/common/constant'
import http from '@/utils/http'

export function countPlaceFFhost(pid) {
  return http.get(`${constant.baseUrl}/v2/place/${pid}/ffhosts/count`)
}

export function placeDevices(placeId, query) {
  return http.get(`${constant.baseUrl}/v2/place/${placeId}/devices`, query)
}

export function placeVideos(placeId, query) {
  return http.get(`${constant.baseUrl}/v2/place/${placeId}/videos`, query)
}

export function placeCountPatrol(pid) {
  return http.get(`${constant.baseUrl}/v2/place/${pid}/patrol/count`)
}

export function pageAllAlarms(params) {
  return http.get(`${constant.baseUrl}/v2/alarms/${params.notice_status}`, params)
}

export function getenergytoday(params) {
  return http.get(`${constant.baseUrl}/v2/place/energy/today`, params)
}
export function unbindDevicenew(mac, verifycode) {
  return http.del(`${constant.baseUrl}/v3/device/unbind?mac=` + mac + `&verify_code=` + verifycode)
}

export function getVideoUrl(params) {
  return http.post(`/gethls.json`, params, { timeout: 30000 })
}

export function delVideo(id) {
  return http.del(`${constant.baseUrl}/v2/video/${id}`)
}

export function placePagePatrols(pid, params) {
  return http.get(`${constant.baseUrl}/v2/place/${pid}/patrols`, params)
}


export function placePatrolHistory(pid, params) {
  return http.get(`${constant.baseUrl}/v2/place/${pid}/patrols/history`, params)
}

export function findDevice(mac) {
  return http.get(`${constant.baseUrl}/v2/device/${mac}`)
}

export function deviceAlarmList(mac, params) {
  return http.get(`${constant.baseUrl}/v2/device/${mac}/alarms`, params)
}

export function findband(mac) {
  return http.get(`${constant.baseUrl}/v2/supervisor/${mac}`)
}

export function deviceConfig(mac, cfg) {
  return http.putWithJson(`${constant.baseUrl}/v2/device/${mac}/config`, cfg)
}

export function queryFFAlarms(params) {
  return http.get(`${constant.baseUrl}/v2/ffhost/alarms`, params)
}

export function placeFFhosts(pid) {
  return http.get(`${constant.baseUrl}/v2/place/${pid}/ffhosts`)
}

export function unbindDevice(mac) {
  return http.del(`${constant.baseUrl}/v2/device/${mac}/unbind`)
}

export function getFFhostBrands() {
  return http.get(`${constant.baseUrl}/v2/ffhost/brands`)
}

export function devicebind(data) {
  return http.postWithJson(`${constant.baseUrl}/v2/device/bind`, data)
}

export function placeelectricitylist(pid, params) {
  return http.get(`${constant.baseUrl}/v2/place/${pid}/electricity/total`, params)
}

export function placeelectricity(pid, params) {
  return http.get(`${constant.baseUrl}/v2/place/${pid}/electricity`, params)
}

export function getelectricityreportplace(params) {
  return http.get(`${constant.baseUrl}/v2/device_electricity_report_place`, params)
}

export function nbinspectiongps(params) {
  return http.get(`${constant.baseUrl}/v2/device_nb_gps`, params)
}

export function moveDevice(position) {
  return http.putWithJson(`${constant.baseUrl}/v2/device`, position)
}

export function findAllDevicesall(params) {
  return http.get(`${constant.baseUrl}/v3/devices/all`, params)
}

export function deviceStatus(mac, params) {
  return http.get(`${constant.baseUrl}/v2/device/${mac}/status`, params)
}

export function logHistory(mac, params) {
  return http.get(`${constant.baseUrl}/v2/device/${mac}/history`, params)
}

export function addVideo(item) {
  return http.postWithJson(`${constant.baseUrl}/v2/video`, item)
}

export function updateVideo(item) {
  return http.putWithJson(`${constant.baseUrl}/v2/video`, item)
}
